var xx = {};

function navigateToUrl(url, newwindow = true, is_unescape = true) {
  if (this.ismobile()) {
    window.location.href = url;
    return;
  }
  if (is_unescape) {
    url = unescape(url);
  }
  var f = document.createElement("FORM");
  f.action = url;
  if (newwindow) {
    f.target = '_blank';
  }
  var indexQM = url.indexOf("?");
  if (indexQM >= 0) {
    // the URL has parameters => convert them to hidden form inputs
    var params = url.substring(indexQM + 1).split("&");
    for (var i = 0; i < params.length; i++) {
      var keyValuePair = params[i].split("=");
      var input = document.createElement("INPUT");
      input.type = "hidden";
      input.name = keyValuePair[0];
      input.value = keyValuePair[1];
      f.appendChild(input);
    }
  }
  document.body.appendChild(f);
  console.log("f:", f);
  f.submit();
}


var isMobile = {
  Android: function () {
    return navigator.userAgent.match(/Android/i);
  },
  BlackBerry: function () {
    return navigator.userAgent.match(/BlackBerry/i);
  },
  iOS: function () {
    return navigator.userAgent.match(/iPhone|iPad|iPod/i);
  },
  Opera: function () {
    return navigator.userAgent.match(/Opera Mini/i);
  },
  Windows: function () {
    return navigator.userAgent.match(/IEMobile/i) || navigator.userAgent.match(/WPDesktop/i);
  },
  any: function () {
    return (isMobile.Android() || isMobile.BlackBerry() || isMobile.iOS() || isMobile.Opera() || isMobile.Windows());
  }
};

Vue.prototype.is_mobile_fun = isMobile;

Vue.prototype.ismobile = function () {
  return this.is_mobile_fun.any() != null;
};

var getUrlVars = function (url) {
  var vars = [],
    hash;
  url = url || window.location.href
  var hashes = url.slice(url.indexOf('?') + 1).split('&');
  for (var i = 0; i < hashes.length; i++) {
    hash = hashes[i].split('=');
    vars[hash[0]] = hash[1];
  }
  return vars;
};

function toThousands(num) {
  var num = (num || 0).toString(), result = '';
  while (num.length > 3) {
    result = ',' + num.slice(-3) + result;
    num = num.slice(0, num.length - 3);
  }
  if (num) {
    result = num + result;
  }
  return result;
}


var updateQueryStringParameter = function updateQueryStringParameter(uri, key, value) {
  if (!value) {
    return uri;
  }
  var re = new RegExp("([?&])" + key + "=.*?(&|$)", "i");
  var separator = uri.indexOf('?') !== -1 ? "&" : "?";
  if (uri.match(re)) {
    return uri.replace(re, '$1' + key + "=" + value + '$2');
  } else {
    return uri + separator + key + "=" + value;
  }
}

function getDateDiff(dateTimeStamp) {
  var result = '';
  var minute = 1000 * 60;
  var hour = minute * 60;
  var day = hour * 24;
  var halfamonth = day * 15;
  var month = day * 30;
  var now = new Date().getTime();
  var diffValue = now - dateTimeStamp;
  if (diffValue < 0) {
    return;
  }
  var monthC = diffValue / month;
  var weekC = diffValue / (7 * day);
  var dayC = diffValue / day;
  var hourC = diffValue / hour;
  var minC = diffValue / minute;
  if (monthC >= 1) {
    result = "" + parseInt(monthC) + "月前";
  } else if (weekC >= 1) {
    result = "" + parseInt(weekC) + "周前";
  } else if (dayC >= 1) {
    result = "" + parseInt(dayC) + "天前";
  } else if (hourC >= 1) {
    result = "" + parseInt(hourC) + "小时前";
  } else if (minC >= 1) {
    result = "" + parseInt(minC) + "分钟前";
  } else
    result = "刚刚";
  return result;
}

function getdate(timestamp, add_time = false) {
  timestamp = parseInt(timestamp);
  var now = new Date(timestamp),
    y = now.getFullYear(),
    m = now.getMonth() + 1,
    d = now.getDate();
  var s = y + "-" + (m < 10 ? "0" + m : m) + "-" + (d < 10 ? "0" + d : d);
  if (add_time) {
    s = s + " " + now.toTimeString().substr(0, 8);
  }
  return s
}


var showDate = function (timestamp) {
  var dtime = xx.init_data.now_time - timestamp;
  if (dtime > 60 * 60 * 24 * 7) {
    return xx.getdate(timestamp * 1000);
  }
  return xx.getDateDiff(timestamp * 1000)
}

